  width: 100vw;
  background-color: #14d9c4;
  opacity: 0.9;
  color: white;
}

.header_navbar-list {
  display: flex;
  justify-content: space-between;
}

.header_navbar_item {
  list-style: none;
}

a {
  color: inherit;
  font-weight: bolder;
  text-decoration: none;
}/* FOOTER */.footer {
